import React, { useContext, useState, useEffect, useRef } from 'react';
import styles from './CanvasToolbar.module.css';
import ResizeDropdown from './ResizeDropdown/ResizeDropdown';
import TemplatesDropdown from './TemplatesDropdown/TemplatesDropdown';
import EditorContext from '../../contexts/EditorContext';
import Backdrop from '../../components/Backdrop/Backdrop';
import { ReactComponent as Templates } from '../../assets/editor/templates.svg';
import { ReactComponent as Resize } from '../../assets/editor/resize.svg';
import { ReactComponent as Crop } from '../../assets/editor/cropImage.svg';
import { SketchPicker } from 'react-color';
import { useHistory } from 'react-router-dom';
import useGetTemplates from '../../hooks/useGetTemplates';
import useCreateTemplate from '../../hooks/useCreateTemplate';
import useSaveTemplate from './hooks/useSaveTemplate';

// const templateType = 'userOnly';
// const templateTypeOption = 'userTemplate';
const templateType = 'common';
const templateTypeOption = 'commonTemplate';

const CanvasToolbar = ({
  setVideoData,
  info,
  setSpeakers,
  canvas,
  setTemplateToLoad,
}) => {
  const { editorState, editorDispatch } = useContext(EditorContext);
  const { isGettingTemplates, getTemplates, templates, setTemplates } =
    useGetTemplates();
  const {
    isCreatingTemplate,
    createTemplate,
    createdTemplate,
    setCreatedTemplate,
  } = useCreateTemplate();
  const [shouldGetTemplates, setShouldGetTemplates] = useState(false);
  const [bgColor, setBgColor] = useState(
    editorState.canvasState.backgroundColor
  );
  const backgroundColorRef = useRef(null);
  const resizeDropdownRef = useRef(null);
  const [currentTemplateOption, setCurrentTemplateOption] = useState(null);

  const history = useHistory();
  useSaveTemplate(
    editorState,
    editorDispatch,
    canvas,
    isCreatingTemplate,
    createTemplate,
    templateType
  );

  const clickHandler = (e) => {
    if (!editorState.shouldCropMedia && !editorState.isLoadingTemplate) {
      if (editorState.isCropMode) {
        editorDispatch({ type: 'setShouldCropMedia', data: true });
      } else {
        let id = e.currentTarget.id;
        if (id === 'resize') {
          editorDispatch({ type: 'setIsResizeDropdown', data: true });
        } else if (id === 'bgColor') {
          editorDispatch({ type: 'setIsCanvasBgColorDropdown', data: true });
        } else if (id === 'crop') {
          setVideoData({ ...info });
          let objects = canvas.getObjects();
          let speakerVideos = [];
          for (let i = 0; i < objects.length; i++) {
            let obj = objects[i];
            if (obj.type === 'video') speakerVideos.push({ ...obj.cropRect });
          }
          setSpeakers(speakerVideos);
          history.push(`/editor/speakers`);
        } else if (id === 'templates') {
          editorDispatch({ type: 'setIsTemplatesDropdown', data: true });
        }
      }
    }
  };

  const colorChangeCompleteHandler = (c) => {
    if (backgroundColorRef.current) {
      let colorString = `rgba(${c.rgb.r}, ${c.rgb.g}, ${c.rgb.b}, ${c.rgb.a})`;
      backgroundColorRef.current.style.background = colorString;
      editorDispatch({ type: 'setCanvasBackgroundColor', data: colorString });
    }
  };

  useEffect(() => {
    backgroundColorRef.current.style.background = bgColor;
  }, [bgColor]);

  useEffect(() => {
    if (shouldGetTemplates) {
      setShouldGetTemplates(false);
      getTemplates();
    }
  }, [shouldGetTemplates, getTemplates]);

  useEffect(() => {
    if (templates) {
      setCurrentTemplateOption((prevState) => {
        if (prevState) {
          return {
            type: prevState.type,
            templates: [...templates[prevState.type]],
          };
        } else {
          return {
            type: 'commonTemplate',
            templates: [...templates.commonTemplate],
          };
        }
      });
    }
  }, [templates]);

  useEffect(() => {
    if (createdTemplate) {
      setTemplates((prevState) => {
        let type = createdTemplate.type;
        if (type === 'common') {
          return {
            ...prevState,
            commonTemplate: [...templates.commonTemplate, createdTemplate],
          };
        } else if (type === 'userOnly') {
          return {
            ...prevState,
            userTemplate: [...templates.userTemplate, createdTemplate],
          };
        } else return { ...prevState };
      });
      setCreatedTemplate(null);
    }
  }, [createdTemplate, setTemplates, templates, setCreatedTemplate]);

  return (
    <div className={styles.CanvasToolbar}>
      <div
        className={styles.TemplatesContainer}
        id='templates'
        onClick={clickHandler}
      >
        <Templates />
        <p className={styles.OptionText}>Templates</p>
      </div>
      <div className={styles.BorderDiv}></div>
      <div
        className={styles.ResizeContainer}
        id='resize'
        onClick={clickHandler}
      >
        <Resize />
        <p className={styles.OptionText}>Resize</p>
      </div>
      <div className={styles.BorderDiv}></div>
      <div className={styles.CropContainer} id='crop' onClick={clickHandler}>
        <Crop />
        <p className={styles.OptionText}>Crop</p>
      </div>
      <div className={styles.BorderDiv}></div>
      <div
        className={styles.ColorContainer}
        id='bgColor'
        onClick={clickHandler}
      >
        <div className={styles.ColorPickerBorder}>
          <div ref={backgroundColorRef} className={styles.ColorPicker}></div>
        </div>
        <p className={styles.OptionText}>Background color</p>
      </div>

      {editorState.isTemplatesDropdown ? (
        <>
          <Backdrop></Backdrop>
          <div className={styles.TemplatesDropdownContainer}>
            <TemplatesDropdown
              isCreatingTemplate={isCreatingTemplate}
              getTemplates={getTemplates}
              templates={templates}
              setShouldGetTemplates={setShouldGetTemplates}
              currentOption={currentTemplateOption}
              setCurrentOption={setCurrentTemplateOption}
              setTemplateToLoad={setTemplateToLoad}
              isGettingTemplates={isGettingTemplates}
              templateTypeOption={templateTypeOption}
            />
          </div>
        </>
      ) : null}

      {editorState.isResizeDropdown ? (
        <>
          <Backdrop></Backdrop>
          <div className={styles.ResizeDropdown} ref={resizeDropdownRef}>
            <ResizeDropdown />
          </div>
        </>
      ) : null}

      {editorState.isCanvasBgColorDropdown ? (
        <>
          <Backdrop></Backdrop>
          <div className={styles.ColorDropdownContainer}>
            <SketchPicker
              color={bgColor}
              onChange={(c) => setBgColor(c.rgb)}
              onChangeComplete={colorChangeCompleteHandler}
            />
          </div>
        </>
      ) : null}
    </div>
  );
};

export default CanvasToolbar;
