import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useCreateTemplate = () => {
  const [isCreatingTemplate, setIsCreatingTemplate] = useState(false);
  const [createdTemplate, setCreatedTemplate] = useState(null);
  const isMounted = useRef(false);

  const axiosCreateTemplate = useCallback((data) => {
    return new Promise((resolve, reject) => {
      axios
        .post('/template', data)
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const createTemplate = useCallback(
    async (data) => {
      setCreatedTemplate(null);
      setIsCreatingTemplate(true);
      try {
        const res = await axiosCreateTemplate(data);
        if (isMounted.current) setCreatedTemplate(res.data);
        console.log(res);
      } catch (uploadError) {
        console.log('Error Creating Template', uploadError);
      } finally {
        if (isMounted.current) setIsCreatingTemplate(false);
        return;
      }
    },
    [axiosCreateTemplate]
  );

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return {
    isCreatingTemplate,
    createTemplate,
    createdTemplate,
    setCreatedTemplate,
  };
};

export default useCreateTemplate;
