import { useState, useEffect } from 'react';
import { fabric } from 'fabric';

const useAddCanvas = (
  canvas,
  setCropCanvas
) => {
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  useEffect(() => {
    if(canvas && isFirstLoad) {
      setIsFirstLoad(false)
      let fabricCanvas = new fabric.Canvas('cropCanvas', {
        width: 0,
        height: 0,
        preserveObjectStacking: true,
        fireRightClick: true,
        stopContextMenu: true,
        selection: false,
      });
      setCropCanvas(fabricCanvas)
    }
  }, [canvas, isFirstLoad, setCropCanvas])
};

export default useAddCanvas;