import { useEffect, useCallback, useState } from 'react';
import {
  prepareMediaForCrop,
  cropMedia,
  cropFinishHandler,
} from '../utils/mediaCrop';
import { restrictActive, restrictBox } from '../utils/restrict';

const useCropMedia = (
  state,
  dispatch,
  canvas,
  cropCanvas,
  eventDispatch,
  assets
) => {
  const [activeInfo, setActiveInfo] = useState(null);
  const [activeEl, setAcitveEl] = useState(null);
  const [activeCopy, setActiveCopy] = useState(null);
  const [holeBox, setHoleBox] = useState(null);
  const [cropbox, setCropbox] = useState(null);
  const [croppedMedia, setCroppedMedia] = useState(null);

  const clearState = useCallback(() => {
    setActiveInfo(null);
    setActiveCopy(null);
    setAcitveEl(null);
    setCropbox(null);
    setCroppedMedia(null);
  }, []);

  // Crop Media
  useEffect(() => {
    if (state.shouldCropMedia && activeEl && cropbox && activeCopy) {
      cropMedia(activeEl, cropbox, activeCopy, setCroppedMedia, cropCanvas);
    } else if (state.shouldCropMedia) {
      setCroppedMedia(activeEl);
    }
  }, [state.shouldCropMedia, activeEl, cropbox, activeCopy, cropCanvas]);

  // If Cropped Media
  useEffect(() => {
    if (croppedMedia && state.shouldCropMedia) {
      cropFinishHandler(
        canvas,
        cropCanvas,
        dispatch,
        croppedMedia,
        state.cropType,
        activeInfo,
        eventDispatch,
        clearState
      );
    }
  }, [
    activeInfo,
    croppedMedia,
    state,
    dispatch,
    canvas,
    cropCanvas,
    eventDispatch,
    clearState,
  ]);

  /// Preparation For Crop
  useEffect(() => {
    if (state.isCropMode) {
      let active = canvas.getActiveObject();
      if (active && active.originalWidth > 0 && active.originalHeight > 0) {
        setAcitveEl(active);
        setActiveInfo({
          cornerRadius: active.cornerRadius,
          width: active.width,
          height: active.height,
          top: active.top,
          left: active.left,
          angle: active.angle,
          strokeWidth: active.strokeWidth,
          cropRect: active.cropRect,
        });
        prepareMediaForCrop(
          active,
          canvas,
          cropCanvas,
          setActiveCopy,
          setCropbox,
          setHoleBox,
          state.cropType,
          assets
        );
      } else {
        eventDispatch({ type: 'setDbClickEvent', data: null });
        dispatch({ type: 'setIsCropMode', data: false });
      }
    }
  }, [
    state.isCropMode,
    state.cropType,
    canvas,
    cropCanvas,
    dispatch,
    eventDispatch,
    assets,
  ]);

  /// If Cropbox and Active El start Crop
  useEffect(() => {
    if (cropbox && activeCopy && holeBox) {
      restrictBox(activeCopy, cropbox, holeBox);
      restrictActive(activeCopy, cropbox);
    }
    return () => {
      if (activeCopy) {
        activeCopy.off('moving');
        activeCopy.off('scaling');
      }
      if (cropbox) {
        cropbox.off('moving');
        cropbox.off('scaling');
      }
    };
  }, [cropbox, activeCopy, holeBox]);

  // Listen to Canvas Click
  useEffect(() => {
    if (cropbox && state.isCropMode) {
      cropCanvas.on('mouse:down', (e) => {
        if (
          !e.target ||
          (e.target.type !== 'cropbox' && e.target.id !== 'media')
        ) {
          dispatch({ type: 'setShouldCropMedia', data: true });
          dispatch({ type: 'setShowToolbar', data: false });
        }
        //  else {
        //   cropCanvas.setActiveObject(activeCopy);
        //   cropCanvas.renderAll();
        // }
      });
      cropCanvas.on('mouse:over', (e) => {
        if (
          e.target &&
          (e.target.type === 'cropbox' || e.target.id === 'media')
        ) {
          cropCanvas.setActiveObject(e.target);
          cropCanvas.renderAll();
        }
      });
    }

    return () => {
      if (cropCanvas) {
        cropCanvas.off('mouse:over');
        cropCanvas.off('mouse:down');
      }
    };
  }, [cropbox, state, dispatch, cropCanvas, activeCopy]);
};

export default useCropMedia;
