import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useGetTemplates = () => {
  const [isGettingTemplates, setIsGettingTemplates] = useState(false);
  const [templates, setTemplates] = useState(null);
  const isMounted = useRef(false);

  const axiosGetTemplates = useCallback(() => {
    return new Promise((resolve, reject) => {
      axios
        .get('/template')
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const getTemplates = useCallback(async () => {
    setTemplates(null);
    setIsGettingTemplates(true);
    try {
      const res = await axiosGetTemplates();
      if (isMounted.current) setTemplates(res.data);
      console.log(res);
    } catch (uploadError) {
      console.log('Error Getting Templates', uploadError);
    } finally {
      if (isMounted.current) setIsGettingTemplates(false);
      return;
    }
  }, [axiosGetTemplates]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return { isGettingTemplates, getTemplates, templates, setTemplates };
};

export default useGetTemplates;
