import { fabric } from 'fabric';
import {
  getOverlayBox,
  getMediaRectCropBox,
  getRectHoleBox,
  getMediaCircleCropBox,
  getCircleHoleBox,
} from './box';

const reverseMedia = (active) => {
  let cropRect = active.cropRect;
  if (cropRect) {
    active.top = active.top - cropRect.y * active.scaleY;
    active.left = active.left - cropRect.x * active.scaleX;
    active.width = active.originalWidth;
    active.height = active.originalHeight;
    active.cropRect = {
      x: 0,
      y: 0,
      w: active.width,
      h: active.height,
    };
  }
  active.setCoords();
};

export const prepareMediaForCrop = (
  active,
  canvas,
  cropCanvas,
  setActiveCopy,
  setCropbox,
  setHoleBox,
  cropType,
  assets
) => {
  canvas.discardActiveObject();
  active.visible = false;
  active.angle = 0;
  active.setCoords();
  active.clipPath = null;
  active.strokeWidth = 0;
  let isVideo = active.type === 'video';
  let isRectCrop = cropType === 'rect';
  let box = null;
  let holeBox = null;
  if (isRectCrop) {
    box = getMediaRectCropBox(active);
    holeBox = getRectHoleBox(box);
  } else {
    box = getMediaCircleCropBox(active);
    holeBox = getCircleHoleBox(box);
  }
  let ObjectClass = isVideo ? fabric.Video : fabric.CustomImage;
  let mediaAsset = null;
  if (!isVideo) {
    mediaAsset = assets.filter((asset) => asset.src === active.src)[0];
  } else mediaAsset = active._element;
  cropCanvas.add(getOverlayBox(cropCanvas));

  if (mediaAsset) {
    cropCanvas.add(holeBox);
    let media = new ObjectClass(mediaAsset, {
      originalWidth: active.originalWidth,
      originalHeight: active.originalHeight,
      left: active.left,
      top: active.top,
      width: active.width,
      height: active.height,
      scaleX: active.scaleX,
      scaleY: active.scaleY,
      cornerRadius: active.cornerRadius,
      excludeFromExport: true,
      id: 'media',
      // borderColor: 'rgba(0, 0, 0, 0)',
      cropRect: active.cropRect,
      globalCompositeOperation: 'destination-atop',
    });
    media.setControlsVisibility({
      mtr: false,
      // ml: false,
      // mr: false,
      // mt: false,
      // mb: false,
    });
    cropCanvas.add(media);
    reverseMedia(media);
    setActiveCopy(media);
    cropCanvas.setActiveObject(media);

    cropCanvas.add(box);
    cropCanvas.requestRenderAll();
    setHoleBox(holeBox);
    setCropbox(box);
  } else {
    active.clone((cloned) => {
      cropCanvas.add(holeBox);
      cloned.excludeFromExport = true;
      cloned.id = 'media';
      cloned.visible = true;
      cloned.globalCompositeOperation = 'destination-atop';
      cropCanvas.add(cloned);
      reverseMedia(cloned);
      setActiveCopy(cloned);
      cropCanvas.setActiveObject(cloned);
      cloned.setControlsVisibility({
        mtr: false,
        // ml: false,
        // mr: false,
        // mt: false,
        // mb: false,
      });
      cropCanvas.add(box);
      cropCanvas.requestRenderAll();
      setHoleBox(holeBox);
      setCropbox(box);
    });
  }
};

export const cropMedia = (active, box, activeCopy, setCropped, canvas) => {
  let ratioX = box.scaleX / activeCopy.scaleX;
  let ratioY = box.scaleY / activeCopy.scaleY;
  let newTop = (box.top - activeCopy.top) / activeCopy.scaleY;
  let newLeft = (box.left - activeCopy.left) / activeCopy.scaleX;
  active.width = box.width * ratioX;
  active.height = box.height * ratioY;
  active.scaleX = activeCopy.scaleX;
  active.scaleY = activeCopy.scaleY;
  active.cropRect = {
    x: newLeft,
    y: newTop,
    w: box.width * ratioX,
    h: box.height * ratioY,
  };

  if (box.isOnScreen()) {
    active.top = box.top;
    active.left = box.left;
  } else {
    let zoom = canvas.getZoom();
    let canvasWidth = canvas.getWidth() / zoom;
    let canvasHeight = canvas.getHeight() / zoom;
    if (box.top > canvasHeight) {
      active.top = canvasHeight - active.height * active.scaleY;
    } else if (box.top < 0) active.top = 0;
    if (box.left > canvasWidth) {
      active.left = canvasWidth - active.width * active.scaleX;
    } else if (box.left < 0) active.left = 0;
  }

  setCropped(active);
};

export const cropFinishHandler = (
  canvas,
  cropCanvas,
  dispatch,
  obj,
  cropType,
  info,
  eventDispatch,
  clearState
) => {
  obj.visible = true;
  obj.angle = info.angle;
  canvas.setActiveObject(obj);
  let type = obj.type;
  obj.setControlsVisibility({
    mtr: type === 'customImage',
    // ml: true,
    // mr: true,
    // mt: true,
    // mb: true,
  });
  if (cropType === 'circle') obj.cornerRadius = 100;
  dispatch({
    type: 'setCurrentObject',
    data: { type, object: obj },
  });

  if (obj.cornerRadius > 0) {
    obj.strokeWidth = 0;
    dispatch({
      type: type === 'video' ? 'setVideoCornerRadius' : 'setImageCornerRadius',
      data: obj.cornerRadius,
    });
  } else if (type === 'video') {
    dispatch({
      type: 'setVideoStrokeWidth',
      data: obj.strokeWidth,
    });
  }
  obj.setCoords();
  if (type !== 'video') obj.setControlsVisibility({ mtr: true });
  cropCanvas.clear();
  clearState();
  canvas.requestRenderAll();
  eventDispatch({ type: 'setDbClickEvent', data: null });
  dispatch({ type: 'setShouldCropMedia', data: false });
  dispatch({ type: 'setIsCropMode', data: false });
  dispatch({ type: 'setCurrentCoords', data: obj.lineCoords });
  dispatch({ type: 'setShowToolbar', data: true });
  dispatch({ type: 'setShouldTriggerCanvasUpdate', data: true });
};
