import React, { useState, useContext, useRef } from 'react';
import styles from './CropCanvas.module.css';
import EditorContext from '../../../contexts/EditorContext';
import EventContext from '../../../contexts/EventContext';
import useCropMedia from '../hooks/useCropMedia';
import useAddCanvas from './hooks/useAddCanvas';
import useUpdateCanvas from './hooks/useUpdateCanvas';

const CropCanvas = ({ canvas, userAssets }) => {
  const { editorState, editorDispatch } = useContext(EditorContext);
  const { eventDispatch } = useContext(EventContext);
  const [cropCanvas, setCropCanvas] = useState(null);
  const cropCanvasRef = useRef(null);

  useAddCanvas(canvas, setCropCanvas);
  useCropMedia(
    editorState,
    editorDispatch,
    canvas,
    cropCanvas,
    eventDispatch,
    userAssets
  );
  useUpdateCanvas(
    cropCanvas,
    editorState.canvasState,
    editorState.isCropMode,
    cropCanvasRef
  );

  const backdropClickHandler = () => {
    editorDispatch({ type: 'setShouldCropMedia', data: true });
    editorDispatch({ type: 'setShowToolbar', data: false });
  };

  return (
    <>
      <div className={styles.CropCanvas} ref={cropCanvasRef}>
        <canvas id='cropCanvas' />
      </div>
      {editorState.isCropMode ? (
        <div className={styles.Backdrop} onClick={backdropClickHandler} />
      ) : null}
    </>
  );
};

export default CropCanvas;
