import { roundedCorners, loadAndUse } from './updateObject';
import { getResizeValues } from '../../../utils/canvasUtils';

const positionObject = (templateEl, currentEl) => {
  let width = currentEl.width;
  let height = currentEl.height;
  let x = templateEl.left;
  let y = templateEl.top;

  if (
    templateEl.width * templateEl.scaleX > currentEl.originalWidth ||
    templateEl.height * templateEl.scaleY > currentEl.originalHeight
  ) {
    let heightRatio = templateEl.height / templateEl.originalHeight;
    let widthRatio = templateEl.width / templateEl.originalWidth;
    width = currentEl.originalWidth * widthRatio;
    height = currentEl.originalHeight * heightRatio;

    let scaleX = (templateEl.width * templateEl.scaleX) / width;
    let scaleY = (templateEl.height * templateEl.scaleY) / height;
    let scale = Math.min(scaleX, scaleY);
    templateEl.scaleX = scale;
    templateEl.scaleY = scale;
  } else {
    width = templateEl.width * templateEl.scaleX;
    height = templateEl.height * templateEl.scaleY;
    templateEl.scaleX = 1;
    templateEl.scaleY = 1;
  }

  if (x + width > currentEl.originalWidth) {
    x -= x + width - currentEl.originalWidth;
  }
  if (y + height > currentEl.originalHeight) {
    y -= y + height - currentEl.originalHeight;
  }

  templateEl.originalWidth = currentEl.originalWidth;
  templateEl.originalHeight = currentEl.originalHeight;
  templateEl.width = width;
  templateEl.height = height;
  templateEl.cropRect = {
    x,
    y,
    w: width,
    h: height,
  };
  templateEl.src = currentEl.src;
  if (templateEl.cornerRadius !== 0) {
    templateEl.set(
      'clipPath',
      roundedCorners(templateEl, templateEl.cornerRadius)
    );
  }
  templateEl.setCoords();
};

const finishHandler = (
  canvas,
  lastId,
  videos,
  state,
  template,
  setSpeakerVideos,
  setObjectIdCount,
  dispatch,
  speakerVideos
) => {
  let currentEl = null;
  for (let i = 0; i < videos.length; i++) {
    let templateEl = videos[i];
    if (speakerVideos[i]) currentEl = speakerVideos[i];
    if (currentEl) positionObject(templateEl, currentEl);
    else canvas.remove(templateEl);
  }

  let { width, height, resize } = getResizeValues(
    state.canvasState.initialWidth,
    state.canvasState.initialHeight,
    template.resize
  );

  dispatch({ type: 'setVideoSize', data: { width, height, resize } });
  setObjectIdCount(lastId);
  setSpeakerVideos(videos);
  dispatch({ type: 'setIsLoadingTemplate', data: false });
};

export const loadCanvas = (
  canvas,
  template,
  isMounted,
  state,
  setSpeakerVideos,
  setObjectIdCount,
  dispatch,
  speakerVideos,
  setFabricSub
) => {
  canvas.clear();
  let videos = [];
  let lastId = 1;
  canvas.loadFromJSON(template, () => {
    if (isMounted.current) {
      let objects = canvas.getObjects();
      for (let i = 0; i < objects.length; i++) {
        let obj = objects[i];
        if (obj.id === 'subtitle') {
          setFabricSub(obj);
          if (!state.isSubtitles) {
            dispatch({ type: 'setIsSubtitles', data: true });
          }
          dispatch({
            type: 'setSubtitlesState',
            data: {
              fontFamily: obj.fontFamily,
              fontStyle: obj.fontStyle,
              fontWeight: obj.fontWeight,
              fontSize: obj.fontSize,
              fill: obj.fill,
              backgroundColor: obj.backgroundColor,
            },
          });
          loadAndUse(canvas, obj, obj.fontFamily);
        } else {
          obj.id = lastId;
          lastId++;
          if (obj.type === 'customTextbox') {
            loadAndUse(canvas, obj, obj.fontFamily);
          } else if (obj.type === 'video') videos.push(obj);
        }
      }

      finishHandler(
        canvas,
        lastId,
        videos,
        state,
        template,
        setSpeakerVideos,
        setObjectIdCount,
        dispatch,
        speakerVideos,
      );
    }
  });
};
