import { useCallback, useState, useRef, useEffect } from 'react';
import axios from '../axios-instance';

const useDownloadVideo = () => {
  const [isDownloading, setIsDownloading] = useState(false);
  const [isDownloadDataUploaded, setIsDownloadDataUploaded] = useState(false);
  const isMounted = useRef(false);

  const axiosDownloadVideo = useCallback((data) => {
    return new Promise((resolve, reject) => {
      axios
        .post('/download-video', data, {
          headers: {
            'Content-Type': 'application/json',
          },
        })
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const downloadVideo = useCallback(
    async (data) => {
      setIsDownloading(true);
      try {
        const res = await axiosDownloadVideo(data);
        if (isMounted.current && res.data.status === 'ok') {
          setIsDownloadDataUploaded(true);
        }
        console.log(res);
      } catch (err) {
        console.log('Error Downloading Video', err);
      } finally {
        if (isMounted.current) setIsDownloading(false);
        return;
      }
    },
    [axiosDownloadVideo]
  );

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return {
    isDownloading,
    downloadVideo,
    isDownloadDataUploaded,
    setIsDownloadDataUploaded,
  };
};

export default useDownloadVideo;
