import React, {
  useState,
  useContext,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import styles from './Navigation.module.css';
import EditorContext from '../../contexts/EditorContext';
import { prepareCanvas } from './utils/prepareCanvas';
import { prepareBreaks, prepareSubs } from './utils/prepareBreaksAndSubs';
import DesignControls from './DesignControls/DesignControls';
import Navbar from '../../components/Navbar/Navbar';
import { useHistory } from 'react-router-dom';
import Spinner from '../../components/Spinner2/Spinner';
import useGetAssets from '../../hooks/useGetAssets';
import { fabric } from 'fabric';
import { cacheAssets } from '../../utils/cacheAssets';

const Navigation = ({
  canvas,
  videoRef,
  videoCuts,
  duration,
  subList,
  fabricSub,
  setPreviewUrl,
  videoData,
  isUpdatingProject,
  isDownloading,
  downloadVideo,
  isDownloadDataUploaded,
  setIsDownloadDataUploaded,
  words,
  setUserAssets,
}) => {
  const { editorState, editorDispatch } = useContext(EditorContext);
  const { getAssets, assets, setAssets } = useGetAssets();
  const [shouldFetchAssets, setShouldFetchAssets] = useState(true);
  const [downloadData, setDownloadData] = useState(null);
  const [elements, setElements] = useState(null);
  const [videoInfo, setVideoInfo] = useState(null);
  const [breaks, setBreaks] = useState(null);
  const [subs, setSubs] = useState(null);
  const [resizeInfo, setResizeInfo] = useState(null);
  const history = useHistory();
  const isMounted = useRef(false);
  const downloadRef = useRef(false);

  useEffect(() => {
    if (isDownloadDataUploaded) {
      history.push(`/editor/progress/${videoData.id}`);
      // history.push(`/preview/${videoData.id}`);
    }
  }, [isDownloadDataUploaded, history, videoData]);

  useEffect(() => {
    if (downloadData) {
      downloadVideo(downloadData);
      setDownloadData(null);
    }
  }, [downloadData, downloadVideo, history, videoData]);

  useEffect(() => {
    if (videoData && videoData.canvas) {
      let assets = videoData.canvas.objects.filter(
        (obj) => obj.type === 'customImage'
      );
      cacheAssets(assets, setUserAssets, isMounted);
    } else if (videoData) setUserAssets([]);
  }, [setUserAssets, videoData]);

  useEffect(() => {
    if (shouldFetchAssets) {
      setShouldFetchAssets(false);
      getAssets();
    }
  }, [shouldFetchAssets, getAssets]);

  // const imageDownloadHandler = useCallback((elArr) => {
  //   for (let i = 0; i < elArr.length; i++) {
  //     let el = elArr[i];
  //     downloadRef.current.href = el.image;
  //     downloadRef.current.download = `design${i + 1}.png`;
  //     downloadRef.current.click();
  //   }
  //   setElements(null);
  // }, []);

  // useEffect(() => {
  //   if (elements) imageDownloadHandler(elements);
  // }, [elements, imageDownloadHandler]);

  const downloadClickHandler = () => {
    if (editorState.isCropMode) {
      editorDispatch({ type: 'setShouldCropMedia', data: true });
    } else if (canvas && !isDownloading && !isUpdatingProject && words) {
      videoRef.current.pause();
      prepareBreaks(videoCuts, duration, setBreaks);
      if (fabricSub && subList) {
        prepareSubs(subList, fabricSub, editorState.subtitlesState, setSubs);
      } else setSubs({ captions: [], config: {} });
      prepareCanvas(canvas, setElements, setVideoInfo);
      setResizeInfo({
        type: editorState.canvasState.resize,
        w: editorState.canvasState.videoWidth,
        h: editorState.canvasState.videoHeight,
      });
    }
  };

  const saveTemplateClickHandler = () => {
    let pxRatio = 1;
    let newCanvas = new fabric.Canvas();
    let jsonCanvas = JSON.stringify(canvas);
    let zoom = canvas.getZoom();
    newCanvas.loadFromJSON(jsonCanvas, () => {
      newCanvas.setZoom(pxRatio);
      newCanvas.setDimensions({
        width: (canvas.getWidth() / zoom) * pxRatio,
        height: (canvas.getHeight() / zoom) * pxRatio,
      });
      let image = newCanvas.toDataURL({
        format: 'image/jpg',
        quality: 1,
      });
      downloadRef.current.href = image;
      downloadRef.current.download = 'canvas.png';
      downloadRef.current.click();
    });
  };

  // const downloadClickHandler2 = () => {
  //   setPreviewUrl(videoData.url);
  // };

  const prepareData = useCallback(
    (elementsArr, videoData, breaksData, subsData, resize, id) => {
      let elements = [...elementsArr];
      let data = {
        id,
        resize: { ...resize },
        video: { ...videoData },
        elements,
        breaks: [...breaksData],
        subtitles: { ...subsData },
      };
      console.log(data);
      let jsonData = JSON.stringify(data);
      setDownloadData(jsonData);
      setElements(null);
      setResizeInfo(null);
      setBreaks(null);
      setVideoInfo(null);
      setSubs(null);
    },
    []
  );

  useEffect(() => {
    if (elements && videoInfo && breaks && subs && resizeInfo) {
      prepareData(elements, videoInfo, breaks, subs, resizeInfo, videoData.id);
    }
  }, [elements, videoInfo, prepareData, breaks, subs, videoData, resizeInfo]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return (
    <Navbar videoData={videoData}>
      <div className={styles.Navigation}>
        {canvas ? (
          <DesignControls
            canvas={canvas}
            words={words}
            assets={assets}
            setAssets={setAssets}
          />
        ) : null}
        {/* <button
              id='downloadDesign'
              className={styles.DownloadButton}
              onClick={saveTemplateClickHandler}
              style={{ marginRight: '0.7rem' }}
            >
              {!isDownloading ? 'Save Template' : 'Saving...'}
            </button> */}
        {isUpdatingProject ? (
          <Spinner
            style={{
              width: '1.25rem',
              height: '1.25rem',
              marginRight: '10px',
            }}
          />
        ) : (
          <div
            style={{
              width: '1.25rem',
              height: '1.25rem',
              marginRight: '10px',
            }}
          ></div>
        )}
        {canvas && !editorState.isLoadingTemplate ? (
          <button
            id='downloadDesign'
            className={styles.DownloadButton}
            onClick={downloadClickHandler}
          >
            {!isDownloading ? 'Download' : 'Loading...'}
          </button>
        ) : null}

        <a className={styles.DownloadLink} ref={downloadRef} href='/'>
          Download
        </a>
      </div>
    </Navbar>
  );
};

export default Navigation;
