import { useEffect, useState } from 'react';
import { getCanvasObject, getCanvasImage } from '../../../utils/canvasUtils';

const useSaveTemplate = (
  state,
  dispatch,
  canvas,
  isCreatingTemplate,
  createTemplate,
  type
) => {
  const [canvasImage, setCanvasImage] = useState(null);

  useEffect(() => {
    if (state.shouldSaveTemplate && canvasImage) {
      let canvasObject = getCanvasObject(canvas);
      let templateData = {
        ...canvasObject,
        image: canvasImage,
        type: type,
      };
      createTemplate(templateData);
      dispatch({ type: 'setShouldSaveTemplate', data: false });
      setCanvasImage(null);
    }
  }, [
    state.shouldSaveTemplate,
    dispatch,
    canvas,
    createTemplate,
    canvasImage,
    type,
  ]);

  useEffect(() => {
    if (state.shouldSaveTemplate && !isCreatingTemplate) {
      getCanvasImage(canvas, setCanvasImage);
    }
  }, [isCreatingTemplate, state.shouldSaveTemplate, canvas]);
};

export default useSaveTemplate;
