import { useEffect, useRef, useCallback } from 'react';

const useCheckDownloadProgress = (
  isGettingProgress,
  getDownloadProgress,
  downloadProgress,
  params,
  isMounted
) => {
  const intervalRef = useRef(null);

  const checkProgress = useCallback(() => {
    if (intervalRef.current) clearInterval(intervalRef.current);
    let interval = setInterval(() => {
      if (isMounted.current && !isGettingProgress) {
        getDownloadProgress(params.id);
      }
    }, 1500);
    intervalRef.current = interval;
  }, [getDownloadProgress, params, isMounted, isGettingProgress]);

  useEffect(() => {
    if (params && params.id) getDownloadProgress(params.id);
  }, [params, getDownloadProgress]);

  useEffect(() => {
    if (downloadProgress !== null && downloadProgress !== 100) {
      checkProgress();
    } else if (intervalRef.current) clearInterval(intervalRef.current);

    return () => {
      if (intervalRef.current) clearInterval(intervalRef.current);
    };
  }, [checkProgress, downloadProgress]);
};

export default useCheckDownloadProgress;
