import { fabric } from 'fabric';

export const getCanvasObject = (canvas) => {
  let jsonCanvas = canvas.toJSON([
    'id',
    'cornerRadius',
    'isSvg',
    'padding',
    'cursorWidth',
    'rx',
    'ry',
    'noScaleCache',
    'crossOrigin',
  ]);
  let zoom = canvas.getZoom();
  jsonCanvas.width = Math.round(canvas.getWidth() / zoom);
  jsonCanvas.height = Math.round(canvas.getHeight() / zoom);
  jsonCanvas.resize = canvas.resize;
  return jsonCanvas;
};

export const getCanvasImage = (canvas, setImage) => {
  let pxRatio = 1;
  let newCanvas = new fabric.Canvas();
  let jsonCanvas = JSON.stringify(canvas);
  let zoom = canvas.getZoom();
  newCanvas.loadFromJSON(jsonCanvas, () => {
    newCanvas.setZoom(pxRatio);
    newCanvas.setDimensions({
      width: (canvas.getWidth() / zoom) * pxRatio,
      height: (canvas.getHeight() / zoom) * pxRatio,
    });
    let img = newCanvas.toDataURL({
      format: 'image/jpg',
      quality: 1,
    });
    setImage(img);
  });
};

export const getResizeValues = (initialWidth, initialHeight, option) => {
  let width = initialWidth;
  let height = initialHeight;
  let resize = 'original';
  if (option === 'landscape') {
    // width = Math.round((height * 16) / 9);
    // height = Math.round((width * 9) / 16);
    width = 1280;
    height = 720;
    resize = option;
  } else if (option === 'square') {
    // if (height > width) width = height;
    // else height = width;
    width = 1280;
    height = 1280;
    resize = option;
  } else if (option === 'vertical') {
    // if (width > height) width = initialHeight;
    // else width = initialWidth;
    // height = Math.round((width * 16) / 9);
    width = 720;
    height = 1280;
    resize = option;
  }
  return { width, height, resize };
};
