import React, { useContext, useEffect, useRef, useCallback } from 'react';
import EditorContext from '../../../contexts/EditorContext';
import styles from './TemplatesDropdown.module.css';
import useDeleteTemplate from '../../../hooks/useDeleteTemplate';
import Spinner from '../../../components/Spinner2/Spinner';
import uploadSvg from '../../../assets/editor/upload.svg';

const TemplatesDropdown = ({
  isCreatingTemplate,
  templates,
  setShouldGetTemplates,
  currentOption,
  setCurrentOption,
  setTemplateToLoad,
  isGettingTemplates,
  templateTypeOption,
}) => {
  const { editorDispatch } = useContext(EditorContext);
  const { isDeletingTemplate, deleteTemplate, deleteTemplateRes } =
    useDeleteTemplate();
  const templatesRef = useRef(null);

  useEffect(() => {
    if (deleteTemplateRes) setShouldGetTemplates(true);
  }, [deleteTemplateRes, setShouldGetTemplates]);

  useEffect(() => {
    if (!currentOption) setShouldGetTemplates(true);
  }, [currentOption, setShouldGetTemplates]);

  const handleScroll = useCallback((e) => {
    e.preventDefault();
    templatesRef.current.scrollLeft += e.deltaY;
  }, []);

  useEffect(() => {
    let container = templatesRef.current;
    container.addEventListener('wheel', handleScroll);
    return () => container.removeEventListener('wheel', handleScroll);
  }, [handleScroll]);

  const templateOptionClickHanlder = (e) => {
    let id = e.currentTarget.id;
    if (templates && templates[id]) {
      setCurrentOption({
        type: id,
        templates: [...templates[id]],
      });
    }
  };

  const saveAsTemplateClickHandler = () => {
    if (!isCreatingTemplate) {
      editorDispatch({ type: 'setShouldSaveTemplate', data: true });
    }
  };

  const templateClickHandler = (target) => {
    editorDispatch({ type: 'setIsTemplatesDropdown', data: false });
    setTemplateToLoad(target);
  };

  const deleteClickHandler = (target) => {
    let box = window.confirm('Are you sure you want to delete this template?');
    if (box === true) deleteTemplate(target._id);
  };

  return (
    <div className={styles.Dropdown}>
      <ul className={styles.TemplateOptions}>
        <li
          className={[
            styles.TemplateOption,
            currentOption && currentOption.type === 'commonTemplate'
              ? styles.Current
              : null,
          ].join(' ')}
          id='commonTemplate'
          onClick={templateOptionClickHanlder}
        >
          Templates
        </li>
        <li
          className={[
            styles.TemplateOption,
            currentOption && currentOption.type === 'userTemplate'
              ? styles.Current
              : null,
          ].join(' ')}
          id='userTemplate'
          onClick={templateOptionClickHanlder}
        >
          My templates
        </li>
      </ul>
      <ul className={styles.Templates} ref={templatesRef}>
        {currentOption && currentOption.type === 'userTemplate' ? (
          <li
            className={[styles.Template, styles.SaveTemplate].join(' ')}
            id='saveAsTemplate'
            onClick={saveAsTemplateClickHandler}
          >
            {isCreatingTemplate ? (
              <Spinner style={{ width: '40px', height: '40px' }} />
            ) : (
              <>
                <img className={styles.SaveImg} src={uploadSvg} alt='save' />
                <span className={styles.SaveText}>Save as template</span>
              </>
            )}
          </li>
        ) : null}
        {currentOption && !isDeletingTemplate && !isGettingTemplates ? (
          currentOption.templates.map((template) => {
            return (
              <li
                className={styles.Template}
                key={template._id}
                id={template._id}
              >
                {templateTypeOption === currentOption.type ? (
                  <div
                    className={styles.DeleteContainer}
                    onClick={() => deleteClickHandler(template)}
                  >
                    <span className={styles.XLine1}></span>
                    <span className={styles.XLine2}></span>
                  </div>
                ) : null}
                <img
                  src={template.image_url}
                  alt='template'
                  className={styles.TemplateImg}
                  onClick={() => templateClickHandler(template)}
                />
              </li>
            );
          })
        ) : (
          <Spinner style={{ width: '70px', height: '70px' }} />
        )}
      </ul>
    </div>
  );
};

export default TemplatesDropdown;
