import { useEffect } from 'react';
import { setSize } from '../../utils/updateCanvas';

const useUpdateCanvas = (cropCanvas, state, isCropMode, canvasRef) => {
  useEffect(() => {
    if (cropCanvas) {
      cropCanvas.resize = state.resize;
      setSize(state, cropCanvas);
    }
  }, [state, cropCanvas]);

  useEffect(() => {
    if (isCropMode) canvasRef.current.style.display = 'block';
    else canvasRef.current.style.display = 'none';
  }, [isCropMode, cropCanvas, canvasRef]);
};

export default useUpdateCanvas;
