import { useState, useEffect, useRef } from 'react';
import { cacheAssets } from '../../../utils/cacheAssets';
import { loadCanvas } from '../utils/template';

const useLoadTemplate = (
  templateToLoad,
  setTemplateToLoad,
  canvas,
  video,
  editorState,
  dispatch,
  setUserAssets,
  setObjectIdCount,
  setSpeakerVideos,
  speakerVideos,
  setFabricSub
) => {
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [assets, setAssets] = useState(null);
  const isMounted = useRef(false);

  useEffect(() => {
    if (assets) {
      setIsFirstLoad(true);
      setAssets(null);
      setUserAssets(assets);
      loadCanvas(
        canvas,
        templateToLoad,
        isMounted,
        editorState,
        setSpeakerVideos,
        setObjectIdCount,
        dispatch,
        speakerVideos,
        setFabricSub
      );
      setTemplateToLoad(null);
    }
  }, [
    templateToLoad,
    canvas,
    assets,
    editorState,
    setUserAssets,
    setTemplateToLoad,
    setSpeakerVideos,
    setObjectIdCount,
    dispatch,
    speakerVideos,
    setFabricSub
  ]);

  useEffect(() => {
    if (templateToLoad && isFirstLoad) {
      setIsFirstLoad(false);
      dispatch({ type: 'setIsSubtitles', data: false });
      setFabricSub(null);
      dispatch({ type: 'setIsLoadingTemplate', data: true });
      video.pause();
      canvas.clear();
      let assets = templateToLoad.objects.filter(
        (obj) => obj.type === 'customImage'
      );
      cacheAssets(assets, setAssets, isMounted);
    }
  }, [templateToLoad, canvas, dispatch, video, isFirstLoad, setFabricSub]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);
};

export default useLoadTemplate;
