import { useState, useEffect, useRef } from 'react';
import { fabric } from 'fabric';
import { setSize } from '../utils/updateCanvas';
import { loadAndUse } from '../utils/updateObject';

const loadCanvas = (
  canvas,
  canvasInfo,
  shouldLoad,
  isMounted,
  state,
  setCanvas,
  setSpeakerVideos,
  setObjectIdCount,
  setFabricSub,
  dispatch
) => {
  canvas.clear();
  let videos = [];
  let lastId = 1;
  canvas.loadFromJSON(canvasInfo, () => {
    if (isMounted.current) {
      let objects = canvas.getObjects();
      for (let i = 0; i < objects.length; i++) {
        let obj = objects[i];
        if (!shouldLoad && obj.type === 'video') canvas.remove(obj);
        else if (obj.id === 'subtitle') {
          setFabricSub(obj);
          dispatch({ type: 'setIsSubtitles', data: true });
          dispatch({
            type: 'setSubtitlesState',
            data: {
              fontFamily: obj.fontFamily,
              fontStyle: obj.fontStyle,
              fontWeight: obj.fontWeight,
              fontSize: obj.fontSize,
              fill: obj.fill,
              backgroundColor: obj.backgroundColor,
            },
          });
          loadAndUse(canvas, obj, obj.fontFamily);
        } else {
          obj.id = lastId;
          lastId++;
          if (obj.type === 'customTextbox') {
            loadAndUse(canvas, obj, obj.fontFamily);
          } else if (obj.type === 'video') videos.push(obj);
        }
      }

      setSize(state, canvas);
      setObjectIdCount(lastId);
      setCanvas(canvas);
      if (shouldLoad) setSpeakerVideos(videos);
    }
  });
};

const addCanvas = (
  state,
  info,
  shouldLoad,
  setCanvas,
  isMounted,
  setSpeakerVideos,
  setObjectIdCount,
  setFabricSub,
  dispatch
) => {
  let canvas = new fabric.Canvas('canvas', {
    width: state.videoWidth,
    height: state.videoHeight,
    resize: state.resize,
    backgroundColor: 'rgba(255,255,255,1)',
    preserveObjectStacking: true,
    fireRightClick: true,
    stopContextMenu: true,
    selection: false,
  });

  if (info.canvas) {
    loadCanvas(
      canvas,
      info.canvas,
      shouldLoad,
      isMounted,
      state,
      setCanvas,
      setSpeakerVideos,
      setObjectIdCount,
      setFabricSub,
      dispatch
    );
  } else {
    setSize(state, canvas);
    setCanvas(canvas);
  }
};

const useAddCanvas = (
  editorState,
  setCanvas,
  info,
  setIsCanvasData,
  speakers,
  setObjectIdCount,
  setSpeakerVideos,
  userAssets,
  setFabricSub,
  dispatch
) => {
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const isMounted = useRef(false);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  useEffect(() => {
    if (isFirstLoad && editorState.width > 0 && userAssets) {
      let isCanvas = info && info.canvas && info.canvas.objects.length > 0;
      let shouldLoad = speakers.length === 0 && isCanvas;
      if (shouldLoad) setIsCanvasData(true);
      addCanvas(
        editorState,
        info,
        shouldLoad,
        setCanvas,
        isMounted,
        setSpeakerVideos,
        setObjectIdCount,
        setFabricSub,
        dispatch
      );
      setIsFirstLoad(false);
    }
  }, [
    speakers,
    setCanvas,
    editorState,
    isFirstLoad,
    info,
    setIsCanvasData,
    setObjectIdCount,
    setSpeakerVideos,
    userAssets,
    setFabricSub,
    dispatch,
  ]);
};

export default useAddCanvas;
