import React, { useEffect, useRef } from 'react';
import styles from './Download.module.css';
import { useHistory, useParams } from 'react-router-dom';
import useGetDownloadProgress from '../../hooks/useGetDownloadProgress';
import Navbar from '../../components/Navbar/Navbar';
import Progress from '../../components/Progress/Progress';
import Spinner from '../../components/Spinner2/Spinner';
import useCheckDownloadProgress from './hooks/useCheckDownloadPorgress';

const Download = () => {
  const { isGettingProgress, getDownloadProgress, downloadProgress } =
    useGetDownloadProgress();
  const history = useHistory();
  const params = useParams();
  const isMounted = useRef(null);

  useCheckDownloadProgress(
    isGettingProgress,
    getDownloadProgress,
    downloadProgress,
    params,
    isMounted
  );

  useEffect(() => {
    if (downloadProgress === false) history.replace('/home');
  }, [downloadProgress, history]);

  useEffect(() => {
    if (+downloadProgress === 100) {
      setTimeout(() => {
        if (isMounted.current) history.replace(`/preview/${params.id}`);
      }, 1000);
    }
  }, [downloadProgress, history, params]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return (
    <>
      {downloadProgress === null || downloadProgress === false ? (
        <Spinner style={{ width: '100px', height: '100px' }} />
      ) : (
        <>
          <Navbar />
          <div className={styles.Download}>
            <Progress
              progress={downloadProgress}
              heading='Generating your video'
            />
          </div>
        </>
      )}
    </>
  );
};

export default Download;
