import { fabric } from 'fabric';

fabric.RectCropBox = fabric.util.createClass(fabric.Rect);

fabric.RectCropBox.prototype.setControlsVisibility({
  mtr: false,
});

fabric.RectCropBox.prototype.render = (function (render) {
  return function (ctx) {
    render.apply(this, arguments);
    this._renderControls(ctx);
    if (!this.canvas._activeObject) this.canvas._activeObject = this;
  };
})(fabric.RectCropBox.prototype.render);