import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useGetDownloadProgress = () => {
  const [isGettingProgress, setIsGettingProgress] = useState(false);
  const [downloadProgress, setDownloadProgress] = useState(null);
  const isMounted = useRef(false);

  const axiosGetProgress = useCallback((id) => {
    return new Promise((resolve, reject) => {
      axios
        .get(`/download-video-progress/${id}`)
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const getDownloadProgress = useCallback(async (id) => {
    setIsGettingProgress(true);
    try {
      const res = await axiosGetProgress(id);
      if (isMounted.current) {
        let progress = res.data.progress;
        if(progress === null) setDownloadProgress(false);
        else setDownloadProgress(progress);
      } 
      console.log(res);
    } catch (uploadError) {
      console.log('Error Getting Download Progress', uploadError);
    } finally {
      if (isMounted.current) setIsGettingProgress(false);
      return;
    }
  }, [axiosGetProgress]);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return {
    isGettingProgress,
    getDownloadProgress,
    downloadProgress,
  };
};

export default useGetDownloadProgress;
