import { fabric } from 'fabric';

fabric.CircleCropBox = fabric.util.createClass(fabric.Circle);

fabric.CircleCropBox.prototype.setControlsVisibility({
  mt: false,
  mb: false,
  ml: false,
  mr: false,
  mtr: false,
});

fabric.CircleCropBox.prototype.render = (function (render) {
  return function (ctx) {
    render.apply(this, arguments);
    this._renderControls(ctx);
    if (!this.canvas._activeObject) this.canvas._activeObject = this;
  };
})(fabric.CircleCropBox.prototype.render);