import { fabric } from 'fabric';

export const getSpeakerBox = (boxSize, boxId) => {
  return new fabric.SpeakerCropBox({
    ...boxSize,
    fill: 'rgba(111, 122, 208, 0.4)',
    top: 0,
    left: 0,
    borderColor: '#6F7BD0',
    lockScalingFlip: true,
    id: boxId,
  });
};

export const getOverlayBox = (canvas) => {
  let zoom = canvas.getZoom();
  let width = canvas.getWidth() / zoom;
  let height = canvas.getHeight() / zoom;
  let rect = new fabric.Rect({
    left: 0,
    top: 0,
    width,
    height,
    noScaleCache: false,
    fill: 'rgba(0, 0, 0, 0.5)',
    strokeWidth: 0,
    selectable: false,
    excludeFromExport: true,
    rx: 0,
    ry: 0,
    id: 'overlay',
    globalCompositeOperation: 'source-over',
  });
  return rect;
};

export const getCropBoxSize = (obj) => {
  let scaleX = obj.scaleX;
  let scaleY = obj.scaleY;
  let width = obj.width;
  let height = obj.height;
  let cropRect = obj.cropRect;
  if (cropRect) {
    scaleX *= obj.width / obj.originalWidth;
    scaleY *= obj.height / obj.originalHeight;
    width = obj.originalWidth;
    height = obj.originalHeight;
  }
  return { scaleX, scaleY, width, height };
};

export const getCircleCropBoxSize = (obj) => {
  let scaleX = obj.scaleX;
  let scaleY = obj.scaleY;
  let width = obj.width;
  let height = obj.height;
  let cropRect = obj.cropRect;
  let minSize = Math.min(width, height);
  let size = minSize;
  let scale = size === height ? scaleY : scaleX;
  if (cropRect) {
    size = Math.min(obj.originalWidth, obj.originalHeight);
    scale *= minSize / size;
  }
  let radius = size / 2;
  return { scale, radius };
};

export const getMediaCircleCropBox = (obj) => {
  let { scale, radius } = getCircleCropBoxSize(obj);
  return new fabric.CircleCropBox({
    originX: 'left',
    originY: 'top',
    left: obj.left,
    top: obj.top,
    strokeDashArray: [12, 12],
    // borderDashArray: [8, 8],
    // borderColor: 'rgba(111, 122, 208, 1)',
    fill: 'rgba(255, 255, 255, 0)',
    borderColor: 'rgba(0, 0, 0, 0)',
    strokeWidth: 2,
    strokeUniform: true,
    stroke: '#dbff17',
    radius: radius,
    scaleX: scale,
    scaleY: scale,
    selectable: true,
    lockScalingFlip: true,
    type: 'cropbox',
    excludeFromExport: true,
    noScaleCache: false,
    globalCompositeOperation: 'source-over',
  });
};

export const getMediaRectCropBox = (obj) => {
  let { scaleX, scaleY, width, height } = getCropBoxSize(obj);
  return new fabric.RectCropBox({
    // fill: 'rgba(255, 255, 255, 1)',
    fill: 'rgba(255, 255, 255, 0)',
    originX: 'left',
    originY: 'top',
    left: obj.left,
    top: obj.top,
    width: width,
    height: height,
    scaleX,
    scaleY,
    // borderDashArray: [2, 2],
    // borderColor: '#dbff17',
    // borderColor: 'rgba(111, 122, 208, 1)',
    borderColor: 'rgba(0, 0, 0, 0)',
    strokeWidth: 2,
    strokeUniform: true,
    strokeDashArray: [12, 12],
    stroke: '#dbff17',
    selectable: true,
    lockScalingFlip: true,
    type: 'cropbox',
    noScaleCache: false,
    excludeFromExport: true,
    globalCompositeOperation: 'source-over',
  });
};

export const getCircleHoleBox = (obj) => {
  return new fabric.Circle({
    fill: 'rgba(255, 255, 255, 1)',
    originX: 'left',
    originY: 'top',
    top: obj.top,
    left: obj.left,
    radius: obj.radius,
    scaleX: obj.scaleX,
    scaleY: obj.scaleY,
    selectable: false,
    lockScalingFlip: true,
    borderColor: 'rgba(0, 0, 0, 0)',
    stroke: 'rgba(0, 0, 0, 0)',
    strokeWidth: 2,
    strokeUniform: false,
    type: 'holeBox',
    excludeFromExport: true,
    noScaleCache: false,
    globalCompositeOperation: 'destination-out',
  });
};

export const getRectHoleBox = (obj) => {
  return new fabric.Rect({
    fill: 'rgba(255, 255, 255, 1)',
    originX: 'left',
    originY: 'top',
    top: obj.top,
    left: obj.left,
    width: obj.width,
    height: obj.height,
    scaleX: obj.scaleX,
    scaleY: obj.scaleY,
    selectable: false,
    lockScalingFlip: true,
    borderColor: 'rgba(0, 0, 0, 0)',
    stroke: 'rgba(0, 0, 0, 0)',
    strokeWidth: 2,
    strokeUniform: false,
    type: 'holeBox',
    excludeFromExport: true,
    noScaleCache: false,
    globalCompositeOperation: 'destination-out',
  });
};
