import { useEffect, useRef } from 'react';
import { addNewImage, addNewShape, addNewText } from '../utils/addObject';

const useAddObject = (
  state,
  dispatch,
  canvas,
  id,
  updateId,
  userAssets,
  setUserAssets
) => {
  const isMounted = useRef(false);

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  useEffect(() => {
    if (state.shapeToAdd) {
      addNewShape(state, canvas, dispatch, id, updateId);
    } else if (state.imageToAdd) {
      addNewImage(
        state,
        canvas,
        dispatch,
        id,
        updateId,
        userAssets,
        setUserAssets,
        isMounted
      );
    } else if (state.shouldAddText) {
      addNewText(canvas, state, dispatch, id, updateId);
    }
  }, [canvas, dispatch, state, id, updateId, userAssets, setUserAssets]);
};

export default useAddObject;
