import { getSpeakerBox } from './box';
import { restrictBox } from './restrict';

export const handleFirstLoadSpeakers = (
  canvas,
  setBoxArr,
  updateBoxId,
  video,
  speakers
) => {
  let boxes = [];
  for (let i = 0; i < speakers.length; i++) {
    let speaker = speakers[i];
    let size = {
      width: video.width,
      height: video.height,
      scaleX: speaker.w / video.width,
      scaleY: speaker.h / video.height,
    };
    let box = getSpeakerBox(size, i + 1);
    box.top = speaker.y;
    box.left = speaker.x;
    updateBoxId();
    canvas.add(box).setActiveObject(box);
    canvas.requestRenderAll();
    boxes.push(box);
    restrictBox(video, box);
  }
  setBoxArr(boxes);
};

export const addSpeaker = (
  boxArr,
  boxId,
  canvas,
  setBoxArr,
  updateBoxId,
  video
) => {
  let size = null;
  if (boxArr.length === 0) {
    size = {
      width: video.width,
      height: video.height,
      scaleX: video.scaleX / 2,
      scaleY: video.scaleY / 2,
    };
  } else {
    let last = boxArr[boxArr.length - 1];
    size = {
      width: last.width,
      height: last.height,
      scaleX: last.scaleX,
      scaleY: last.scaleY,
    };
  }
  let box = getSpeakerBox(size, boxId);
  updateBoxId();
  canvas.add(box).setActiveObject(box);
  canvas.requestRenderAll();
  setBoxArr((prevState) => [...prevState, box]);
  restrictBox(video, box);
};
