import { useEffect } from 'react';
import { generateSubtitles } from '../utils/generateSubtitles';
import { newSubtitle, displaySub } from '../utils/subtitle.js';
import { findSubWithWordIndex } from '../../../utils/findIndex';

const useSubtitles = (
  state,
  canvas,
  fabricSub,
  setFabricSub,
  words,
  subList,
  setSubList,
  currentSub,
  setCurrentSub,
  currentWordIndex,
  videoCuts,
  shouldRerenderSub,
  setShouldRerenderSub,
  isPlaying,
  videoRef
) => {
  useEffect(() => {
    if (fabricSub && currentSub && !state.isLoadingTemplate) {
      if (shouldRerenderSub) setShouldRerenderSub(false);
      displaySub(fabricSub, currentSub);
    }
  }, [
    fabricSub,
    currentSub,
    shouldRerenderSub,
    setShouldRerenderSub,
    subList,
    state.isLoadingTemplate,
  ]);

  useEffect(() => {
    if (subList && !isPlaying && !state.isLoadingTemplate) {
      let sub = findSubWithWordIndex(currentWordIndex, words, subList);
      if (sub !== null) setCurrentSub(sub);
    }
  }, [
    subList,
    setCurrentSub,
    words,
    currentWordIndex,
    isPlaying,
    state.isLoadingTemplate,
  ]);

  useEffect(() => {
    if (fabricSub && videoCuts) generateSubtitles(words, setSubList);
  }, [fabricSub, videoCuts, setSubList, words]);

  useEffect(() => {
    if (canvas && !fabricSub && state.isSubtitles) {
      if (isPlaying) videoRef.current.pause();
      let sub = newSubtitle(canvas, state);
      canvas.add(sub);
      canvas.requestRenderAll();
      setFabricSub(sub);
    } else if (
      canvas &&
      fabricSub &&
      !state.isSubtitles &&
      !state.isLoadingTemplate
    ) {
      canvas.remove(fabricSub);
      setFabricSub(null);
    }
  }, [
    videoRef,
    isPlaying,
    state,
    fabricSub,
    setFabricSub,
    canvas,
    subList,
    words,
    setSubList,
  ]);
};

export default useSubtitles;
