import { useCallback, useState, useEffect, useRef } from 'react';
import axios from '../axios-instance';

const useDeleteTemplate = () => {
  const [isDeletingTemplate, setIsDeletingTemplate] = useState(false);
  const [deleteTemplateRes, setDeleteTemplateRes] = useState(null);
  const isMounted = useRef(false);

  const axiosDeleteTemplate = useCallback((id) => {
    return new Promise((resolve, reject) => {
      axios
        .delete(`/template/${id}`)
        .then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }, []);

  const deleteTemplate = useCallback(
    async (id) => {
      setDeleteTemplateRes(null);
      setIsDeletingTemplate(true);
      try {
        const res = await axiosDeleteTemplate(id);
        if (isMounted.current) setDeleteTemplateRes(res.data);
        console.log(res);
      } catch (uploadError) {
        console.log('Error Deleting Templates', uploadError);
      } finally {
        if (isMounted.current) setIsDeletingTemplate(false);
        return;
      }
    },
    [axiosDeleteTemplate]
  );

  useEffect(() => {
    isMounted.current = true;
    return () => (isMounted.current = false);
  }, []);

  return { isDeletingTemplate, deleteTemplate, deleteTemplateRes };
};

export default useDeleteTemplate;
